import React, { useState, useEffect } from 'react';
import axios from 'axios';
import Movie from './Movie';
import './App.css';
import './Navigation.css';
import './Home.css';

function App() {
  const [state, setState] = useState({
    isLoading: true,
    movies: []
  });

  // async: 非同期という意味 you have to wait for it 발음은 어씽크
  // JavaScriptとgetMovies関数に少し時間が必要って伝え俺らはそれを待つ。
  const getMovies = async () => {
    // await: what do u want me to wait for? => axios
    //axiosの処理が終わるまで待つ
    const {
      data: {
        data: { movies }
      }
    } = await axios.get('https://yts-proxy.now.sh/list_movies.json?sort_by=rating');
    //movies:movies를 축약해서movies로 대처가능. ES6 isn't stupit
    setState({ movies, isLoading: false });
  };

  useEffect(() => {
    getMovies();
  },[]);


  return (
    <section className="container">
      {state.isLoading ? (
        <div className="loader">
          <span className="loader-text">Loading...</span>
        </div>
      ) : (
        <div className="movies">
          {state.movies.map(movie => {
            return <Movie
              key={movie.id}
              id={movie.id}
              year={movie.year}
              title={movie.title}
              summary={movie.summary}
              poster={movie.medium_cover_image}
              genres={movie.genres}
            />
          })}
        </div>
      )}
    </section>
  )
}

export default App

